.container-row
    min-height: 80vh
    padding: 50px 0
    &:first-child
        height: 100vh
        position: relative
        .overlay
            background-color: rgba(0, 0, 0, 0.5)
            position: absolute
            top: 0
            bottom: 0
            left: 0
            right: 0
            overflow: hidden
        .ant-typography
            color: white !important
        .ant-space
            z-index: 1
    &:nth-child(odd)
        background-color: #eaf1fc
        .ant-typography, .ant-card-meta-description
            color: #201678
    &:nth-child(even)
        background-color: #201678
        .ant-typography
            color: #fff
    .ant-space-item
        width: 100%
        .para.ant-typography
            max-width: 320px
            font-size: 18px
            text-align: left
            @media screen and (min-width: 480px)
                max-width: 420px
    .ant-card
        .ant-card-meta-title
            white-space: unset
    .ant-carousel
        .slick-slide
            display: flex
            justify-content: center
            align-items: middle

    .ant-steps-item-wait, .ant-steps-item-process, .ant-steps-item-finish
        > .ant-steps-item-container 
            > .ant-steps-item-content 
                > .ant-steps-item-title
                    color: #fff
        .ant-steps-item-icon 
            > .ant-steps-icon
                color: #000
        
    .ant-timeline
        .ant-timeline-item-left, .ant-timeline-item-right 
            .ant-timeline-item-content
                margin-bottom: 50px
             
        .ant-timeline-item-left
            .ant-timeline-item-content
                padding-left: 25px
        .ant-timeline-item-right 
            .ant-timeline-item-content
                padding-right: 25px


    .card-special.ant-card
        background-color: #170e64
        border-radius: 48px
        height: 300px
        padding: 20px
        flex-flow: wrap
        display: flex
        align-items: center
        .ant-card-meta-detail
            max-width: 320px
            @media screen and (min-width: 480px)
                max-width: 420px
        h3
            text-align: center
        .ant-card-meta-description
            color: #fff
        @media screen and (min-width: 480px)
            padding: 40px

    .col-card
        padding: 40px 16px
        @media screen and (min-width: 480px)
            padding: 40px

    @media screen and (max-width: 480px)
        .ant-steps-horizontal
            &:not(.ant-steps-label-vertical) 
                .ant-steps-item
                    padding-left: unset !important